class WebService {
  static getEvents(date) {
    return new Promise((resolve, reject) => {
      axios.get('https://jsonp.afeld.me/?url=https://www.sofascore.com/baseball/usa/' + date + '/json').then(function functionName(res) {
        const data = res.data.sportItem.tournaments.filter(x => {
          return soccer.map(x=>{return x.uid}).includes(x.tournament.uniqueId);
        });
        let dat = [];
        /*data.filter(y => {
          y.events = y.events.filter(x => {
            return x.formatedStartDate === date.split('-').reverse().join('.')+'.'
          }).map(x=>{
            let z = x
            z.uniqueId = y.uniqueId
            z.tournament.name = y.tournament.name
            return z
          })
          return y.events.length > 0 ;
        })*/
        data.forEach(y => {
          y.events.forEach(x => {
            if(x.formatedStartDate === date.split('-').reverse().join('.')+'.'){
              let z = x
              z.tournamentName = y.tournament.name
              dat.push(z);
            }
          })
        });
        resolve(
          dat.length > 0 ? dat.sort(function(a, b){return a.startTimestamp - b.startTimestamp}) : [{
            name: 'There is no events for this date , please select another date',
            customId: 'error'
          }]
        );
      }).catch((err) => {
        reject(err)
      })
    });
  }
  static insertGame(game,i) {
    return new Promise((resolve, reject) => {
      axios.post('https://www.mlbstreams100.com/wp-json/addp/'+soccer.find(x=>{return x.uid === i}).id, {
        game: game
      }).then(function(res) {
        resolve(res.data)
      }).catch((err) => {
        reject(err)
      })
    });
  }
}

export default WebService;
